//
//-- Portfolio 
//

.portfolio__nav {
	text-align: center;
	margin-bottom: 2em;
	
	@include tablet-port {
		text-align: left;
	}
	
	&-li {
		display: inline-block;
	
		@include tablet-port {
			display: block;
		}
	}
	
	&-item {
		color: #4d4d4d;
		text-transform: uppercase;
		text-decoration: none;
		font-weight: 600;
		transition: color .2s ease;
		display: inline-block;
		font-size: em(16);
		margin: 0.3em 0.8em;
		
		&:hover, &.is-active {
			color: $color-highlight;
		}
		
		@include tablet-port {
			display: block;
			font-size: em(22);
			margin: 0 0 1.25em;
		}
	}
}

.portfolio__item {
	display: block;
	margin-bottom: 2em;
	text-decoration: none;
	
	@include hover-border;
	
	img {
		max-width: none;
		width: 100%;
	}
	

}